h1.list-title {
  border-bottom: 10px solid #000;
  display: table;
  font-weight: bold;
  margin-top: 120px;
}
.highlights {
  background-color: #000;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.highlights .categories {
  position: absolute;
  bottom: 10px;
  left: 30px;
}
.highlights .categories ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}
.highlights .categories ul li {
  padding: 0px 8px;
  border: 1px solid #fff;
  margin: 5px;
}
.highlights .categories ul li a {
  color: #fff;
}
.highlights img {
  width: 100%;
  height: 100%;
}
.highlights .img,
.highlights .info {
  background-color: #9a258f;
  width: calc(50% - 15px);
}
.highlights .info {
  padding: 25px 20px;
}
.highlights .info hr {
  border-width: 5px;
  border-color: #fff;
}
.highlights h2 {
  font-weight: bold;
  color: #fff;
}
.submenu {
  display: flex;
  align-items: center;
  padding: 10px;
  height: 100px;
  width: 100%;
  position: absolute;
  top: -30px;
}
.submenu > div {
  border-right: 1px solid #fff;
  padding: 10px 20px;
  align-items: center;
  display: flex;
}
.submenu > div:last-child {
  border-right: none;
}
.submenu > div img {
  margin: 0 20px;
}
.submenu p,
.submenu h1 {
  margin-bottom: 0;
  padding: 0;
}
.submenu .show-hide {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
